import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuItem } from 'primeng/primeng';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  private items: MenuItem[];
  display: boolean = false;

  constructor(private router: Router) { }

  ngOnInit() {
    this.items = [
      {
        label: '',
        icon: 'fa-copy',
        items: [{
          label: '',
          icon: 'fa-file-o',
          items: [
            {
              label: '', icon: 'fa-plus', command: (event) => {
                this.router.navigate(['orders/add']);
              }
            },
            {
              label: '', icon: 'fa-table', command: (event) => {
                this.router.navigate(['orders']);
              }
            },
          ]
        },
        {
          label: '',
          icon: 'fa-file-o',
          items: [
            {
              label: '', icon: 'fa-plus', command: (event) => {
                this.router.navigate(['waybills/add']);
              }
            },
            {
              label: '', icon: 'fa-table', command: (event) => {
                this.router.navigate(['waybills']);
              }
            },
          ]
        },
        {
          label: '',
          icon: 'fa-file-o',
          items: [
            {
              label: '', icon: 'fa-plus', command: (event) => {
                this.router.navigate(['inventories/add']);
              }
            },
            {
              label: '', icon: 'fa-table', command: (event) => {
                this.router.navigate(['inventories']);
              }
            },
          ]
        },
        {
          label: '',
          icon: 'fa-file-o',
          items: [
            {
              label: '', icon: 'fa-plus', command: (event) => {
                this.router.navigate(['payments/add']);
              }
            },
            {
              label: '', icon: 'fa-table', command: (event) => {
                this.router.navigate(['payments']);
              }
            },
          ]
        },
        {
          label: '',
          icon: 'fa-file-o',
          items: [
            {
              label: '', icon: 'fa-plus', command: (event) => {
                this.router.navigate(['deliveries/add']);
              }
            },
            {
              label: '', icon: 'fa-table', command: (event) => {
                this.router.navigate(['deliveries']);
              }
            },
          ]
        }
        ]
      },
      {
        label: '',
        icon: 'fa-puzzle-piece',
        items: [
          { label: '', icon: 'fa-search' },
          { label: 'Redo', icon: 'fa-mail-reply' }
        ]
      },
      {
        label: '',
        icon: 'fa-question',
        items: [
          {
            label: '', icon: 'fa-meh-o', command: (event) => {
              this.showDialog();
            }
          },
          { label: '', icon: 'fa-book' }
        ]
      }
    ];
  }

  showDialog() {
    this.display = true;
  }
}



